import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class PlatformService {

  constructor(private plt: Platform) {

  }

  public getPlatformTabs() {
    if(this.plt.is('desktop')){
      return 'top'
    }else {
      return 'bottom'
    }
  }

  public getPlatform() {
    if(this.plt.is('desktop')){
      return 'desktop'
    }else {
      return 'mobile'
    }
  }
}
